    export default {
        name: 'comment-detail',
	    data() {
            return {
                detail: {}
            }
	    },
	    onLoad(options) { this.$commonLoad.onload(options);
            this.$showLoading({
                type: 'global',
                text: '加载中...'
            });
            this.$request({
	            url: this.$api.app_admin.comments_reply,
	            data: {
	                id: options.id,
	            }
            }).then(response => {
                this.$hideLoading();
                if(response.code === 0) {
                    this.detail = response.data.list;
                }
            });
	    },
	    methods: {
            screen(index) {
                uni.previewImage({
                	current: index,
                    urls: this.detail.pic_url
                })
            },
            reply() {
                this.$request({
                    url: this.$api.app_admin.comments_reply,
	                method: 'post',
                    data: {
                        id: this.detail.id,
                        reply_content: this.detail.reply_content,
                    }
                }).then(response => {
                    if (response.code === 0) {
                        uni.navigateBack();
                    }
                });
            },
            bindTextAreaBlur(e) {
                this.detail.reply_content = e.detail.value;
            }
	    }
    }
